import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)
//定义（路由）组件，可以从其他文件import
import Hello from '@/components/Day1/Hello'

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'World',
      component: HelloWorld
    },
		{
    	path: '/Hello',
      name: 'Hello',
      component: Hello
    }
  ]
})




